@import "@/scss/main.scss";

body {
    overflow-x: hidden;
    font-family: $default-font;
    margin: 0;
    cursor: default;
}
.unHiddenForSlide {
    .block__slider img {
        filter: blur(0);
    }
    .block__sliderButtons {
        opacity: 1;
        &-nextBtn,
        &-prevBtn {
            &:hover {
                cursor: pointer;
                width: 4.375rem;
                height: 4.375rem;
                svg {
                    fill: $primary;
                }
            }
        }
    }
}
.hiddenTitle {
    opacity: 0;
    overflow: hidden;
    transition-timing-function: linear, step-end;
}
