@import "@/scss/main.scss";

.cat {
    border-radius: 1.875rem;
    width: 19.6875rem;
    font-size: 0.5625rem;
    .cat__header {
        background: $darkdarkblue;
        width: 100%;
        .month {
            width: 100%;
            height: 2.875rem;
            .leftArrow {
                padding-top: 0;
                transform: rotate(180deg);
            }
            svg {
                width: 2.25rem;
                height: 2.25rem;
                stroke: $beige;
            }
            .month-name {
                width: 11.25rem;
                font-family: Playfair Display;
                font-size: 1.4375rem;
                line-height: 2.9375rem;
                letter-spacing: -0.03rem;
            }
        }
        .weekdays {
            color: $beige;
            height: 1.875rem;
            div {
                width: 4.1875rem;
            }
        }
    }
    .grid-hiderows {
        grid-template-rows: var(--calendarGrid);
    }
}
@media only screen and (min-width: $sm-breakpoint) {
    .cat {
        position: absolute;
    }
}
@media only screen and (min-width: $md-breakpoint) {
    .cat {
        height: fit-content;
        overflow: hidden;
        user-select: none;
        border-radius: 1.875rem;
        width: 29.375rem;
        font-size: $font-20;
        .cat__header {
            background: $darkdarkblue;
            width: 100%;
            height: 6.25rem;
            .month {
                width: 100%;
                height: 4.375rem;
                display: flex;
                justify-content: center;
                .leftArrow {
                    padding-top: 0;
                    padding-bottom: 0.5rem;
                    transform: rotate(180deg);
                }
                svg {
                    padding-top: 0.5rem;
                    width: 3.375rem;
                    height: 3.375rem;
                    stroke: $beige;
                    &:hover {
                        cursor: pointer;
                    }
                }
                .month-name {
                    width: 16.875rem;
                    font-family: Playfair Display;
                    font-size: $font-35;
                    line-height: 2.9375rem;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    text-align: center;
                    letter-spacing: -0.03rem;

                    color: #f5f2ef;
                }
            }
            .weekdays {
                display: flex;
                justify-content: space-between;
                color: $beige;
                div {
                    width: 4.1875rem;
                    height: 1.875rem;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    text-align: center;
                }
            }
        }
        .grid-hiderows {
            grid-template-rows: var(--calendarGrid);
        }
    }
}
