@import "@/scss/main.scss";

.background {
    top: 0;
    background: #472173;
    opacity: 0.5;
    mix-blend-mode: multiply;
}
.block__slider {
    width: 100%;
    max-height: 46.875rem;
    img {
        filter: blur(7px);
        height: 34.375rem;
    }
}
.block__sliderButtons {
    transition: $transition;
    opacity: 0;
    height: 4.375rem;
    max-width: 81.875rem;
    &-nextBtn,
    &-prevBtn {
        width: 3.75rem;
        height: 3.75rem;
        background: $white;
        border-radius: 50%;
        transition: $transition-fast;
        z-index: 1;
        margin-left: 4.6875rem;
        svg {
            width: 2.25rem;
            height: 2.25rem;
            fill: $secondary;
        }
    }
    &-prevBtn {
        margin-right: 4.6875rem;
        transform: rotate(180deg);
    }
}
@media only screen and (min-width: $md-breakpoint) {
    .block__slider img {
        max-height: 46.875rem;
    }
}
