@import "@/scss/main.scss";

@keyframes spinner {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
.spin::before {
    animation: 1.5s linear infinite spinner;
    animation-play-state: inherit;
    border: solid 5px #cfd0d1;
    border-bottom-color: $primary;
    border-radius: 50%;
    content: "";
    height: 40px;
    width: 40px;
    will-change: transform;
}
.OnlinePaymentsBlock {
    left: 0;
    right: 0;
    top: 0;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
}

.containerWave {
    width: 100%;
    min-width: 42.5rem;
    height: 1.375rem;
    opacity: 0;
    fill: $backgroundgrey;
    background: white;
}
.reservationForm {
    padding-bottom: 3.75rem;
    background-color: $backgroundgrey;
    color: $deepblue;
    .container {
        max-width: 48.0625rem;
        padding-left: 1.875rem;
        padding-right: 1.875rem;
        .form-grid {
            grid-template-columns: auto;
            grid-row-gap: 0.625rem;
            p {
                color: $secondary;
                padding-left: 1.25rem;
            }
        }
    }
    &__select-inner,
    input,
    &__select-list div,
    textarea {
        padding: 0;
        padding-left: 1.25rem;
        padding-right: 0.8438rem;
        height: 3.75rem;
        font-size: $font-20;
        border-radius: 0.625rem;
        font-family: $playfair-font;
        background-color: $white;
        border: 0;
        color: $deepblue;
        appearance: none;
        &::placeholder {
            color: $deepblue;
            opacity: 1;
        }
    }
    &__select {
        max-height: 3.75rem;

        svg {
            width: 1.75rem;
            height: 0.9375rem;
            stroke: $secondary;
        }
        &-inner {
            background-color: $white;
            height: 3.75rem;
        }
        &-list {
            grid-template-columns: 100%;
            background-color: white;
            border-radius: 0 0 0.625rem 0.625rem;
            div {
                border-radius: 0;

                z-index: 1;
                width: 100%;
                height: 3.75rem;
                p {
                    padding-left: 1.25rem;
                }
                &:hover {
                    background-color: $backgroundgrey;
                }
            }
        }
    }
    textarea {
        padding-top: 0.625rem;
        margin-bottom: 1.875rem;
        resize: none;
        height: 8.4375rem;
    }
}
@media only screen and (min-width: $md-breakpoint) {
    .containerWave {
        opacity: 1;
    }
    .reservationForm {
        padding-bottom: 5.5rem;
        margin-top: 1.875rem;
        .container {
            padding-top: 6.25rem;
            .form-grid {
                grid-template-columns: auto auto;
                grid-column-gap: 1.875rem;
                grid-row-gap: 1.875rem;
            }
        }
        &__select-inner,
        input,
        &__select-list div,
        textarea {
            font-size: 1.5625rem;
        }
        textarea {
            margin-bottom: 1.875rem;
            grid-column: 1 / -1;
            height: 10.625rem;
        }
    }
}
