@import "@/scss/main.scss";

.white {
    background: white;
}
.row {
    grid-template-columns: repeat(7, 2.8125rem);
    grid-auto-rows: 2.8125rem;
    .days-dates,
    .daysGone {
        width: 2.8125rem;
        height: 2.8125rem;
        margin: auto;
        display: flex;
        justify-content: center;
        align-items: center;
        color: $deepblue;
        border-color: $deepblue;
        border-radius: 50%;
        user-select: none;
        font-size: 0.8125rem;
        cursor: pointer;
    }
    .days-dates:hover {
        border: 0.125rem solid;
        width: 2.25rem;
        height: 2.25rem;
    }
    .daysGone {
        color: rgba(0, 0, 0, 0.25);
    }
    .nextMonthDates {
        border-radius: 0 0 0 0;
        background-color: $medium-beige;
        color: rgba(0, 0, 0, 0.25);
        margin: auto;
        width: 100%;
        height: 100%;
        &:hover {
            margin: auto;
            cursor: pointer;
            border-radius: 0 0 0 0;
        }
    }
    .dateOne {
        border-radius: 1.875rem 0 0 0;
        &:hover {
            border-radius: 1.875rem 0 0 0;
        }
    }
    .dateOneTop {
        border-radius: 0 0 1.875rem 0;
        &:hover {
            border-radius: 0 0 1.875rem 0;
        }
    }
    .firstDateCss {
        border: 0.125rem solid $orange;
        width: 2.25rem;
        height: 2.25rem;
        color: $orange;
    }
    .secondDateCss {
        border: 0.125rem solid $orange;
        width: 2.25rem;
        height: 2.25rem;
        background-color: $orange;
        color: white;
    }
}
@media only screen and (min-width: $md-breakpoint) {
    .row {
        grid-template-columns: repeat(7, 4.1875rem);
        grid-auto-rows: 4.1875rem;
        .days-dates,
        .daysGone {
            width: 3.8125rem;
            height: 3.8125rem;
            font-size: $font-20;
        }
        .days-dates:hover {
            width: 3.5rem;
            height: 3.5rem;
        }
        .nextMonthDates {
            border-radius: 0 0 0 0;
            background-color: $medium-beige;
            color: rgba(0, 0, 0, 0.25);
            margin: auto;
            width: 100%;
            height: 100%;
        }
        .dateOne {
            border-radius: 1.875rem 0 0 0;
            &:hover {
                border-radius: 1.875rem 0 0 0;
            }
        }
        .dateOneTop {
            border-radius: 0 0 1.875rem 0;
            &:hover {
                border-radius: 0 0 1.875rem 0;
            }
        }
        .firstDateCss {
            border: 0.125rem solid $orange;
            width: 3.25rem;
            height: 3.25rem;
            color: $orange;
        }
        .secondDateCss {
            border: 0.125rem solid $orange;
            width: 3.25rem;
            height: 3.25rem;
            background-color: $orange;
            color: white;
        }
    }
}
