@import "@/scss/main.scss";

.reservationForm__additional-info {
    background-color: white;
    border-radius: 1.875rem;
    &-item {
        padding-top: 2.75rem;
        font-size: $font-20;
    }
}
.reservationForm__additional {
    padding-left: 0.9375rem;
    padding-right: 0.9375rem;
    padding-top: 1.75rem;
    padding-bottom: 1.75rem;
    .wrap {
        width: 100%;
    }
    &-item {
        .strawberry,
        .prossecco,
        .tux {
            fill: $secondary;
            stroke: $secondary;
            width: 3.75rem;
            height: 3.75rem;
        }
        div {
            padding-left: 1.625rem;
        }
        span {
            font-size: $font-30;
            font-family: $playfair-font;
        }
        p {
            font-size: $font-18;
        }
        .switch {
            position: relative;
            display: inline-block;
            width: 5.375rem;
            height: 2.875rem;
            margin-left: 5.375rem;
            input {
                opacity: 0;
                width: 0;
                height: 0;
                &:checked + .slider {
                    background-color: $orange;
                }

                &:checked + .slider:before {
                    transform: translateX(2.5rem);
                }
            }
        }
        .slider {
            position: absolute;
            cursor: pointer;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: $secondary;
            transition: $transition;
            border-radius: 2.125rem;
            &:before {
                position: absolute;
                content: "";
                height: 2.5rem;
                width: 2.5rem;
                left: 0.1875rem;
                bottom: 0.1875rem;
                background-color: white;
                transition: $transition;
                border-radius: 50%;
            }
        }
        .info {
            width: 2.875rem;
            height: 2.875rem;
            margin-left: 2.5rem;
            &:hover {
                cursor: pointer;
            }
        }
    }
}
@media only screen and (min-width: $AdditionalComponent-breakpoint) {
    .reservationForm__additional {
        .wrap {
            display: flex;
        }
    }
}
@media only screen and (min-width: $md-breakpoint) {
    .reservationForm__additional {
        padding-left: 2.5rem;
        padding-right: 2.5rem;
        padding-top: 1.25rem;
        padding-bottom: 1.25rem;
        &-item {
            .tux {
                width: 4.375rem;
                height: 4.375rem;
            }
            div {
                padding: 0;
                display: flex;
            }
            span {
                font-size: $font-35;
                padding-left: 2.5rem;
                padding-right: 1.875rem;
            }
        }
    }
}
