@import "@/scss/main.scss";

.header {
    width: 100%;
    margin-top: 1.875rem;

    z-index: 1;
    &__menu {
        width: 3.75rem;
        height: 3.75rem;
        fill: $secondary;
    }
    &__wrap {
        max-width: 90rem;
        // margin-right: 2.1875rem;
        padding-right: 1.875rem;
        padding-left: 1.875rem;
        color: $white;
    }
    &__logo {
        fill: $secondary;
        &-icon {
            width: 2.5rem;
            height: 3rem;
        }
        &-iconBig {
            width: 3.75rem;
            height: 4.25rem;
        }
        &-text {
            font-size: $font-35;
            margin-top: 0.4375rem;
            margin-left: 1.25rem;
        }
    }
    &__contacts {
        width: 55.25rem;
        margin-top: 1rem;
        font-size: 1.5625rem;
        a {
            color: $white;
        }
        svg {
            padding-top: 0.125rem;
            padding-right: 0.3125rem;
            width: 1.5625rem;
            height: 1.5625rem;
            fill: $secondary;
        }
    }
}
@media only screen and (min-width: $md-breakpoint) {
    .header {
        &__logo {
            margin-left: 2.8175rem;
        }
    }
}
