@import "@/scss/main.scss";

.highlight {
    border: solid 0.125rem red;
    border-radius: 0.375rem;
}
.captcha__wrap {
    margin: 1.25rem auto;
}
.cardShow {
    transition: $transition;
    padding: 0.625rem;
    border-radius: 0.625rem;
    background-color: #f2eef5;
}
.cardHide {
    transition: $transition;
    height: 0rem;
}
.containerWave {
    width: 100%;
    min-width: 42.5rem;
    height: 1.375rem;
    fill: $white;
    background: $backgroundgrey;
}
.wrap {
    background-color: $white;
}
.SummaryBlock {
    color: $deepblue;
    font-size: $font-18;
    max-width: 35.625rem;
    padding-right: 1.875rem;
    padding-left: 1.875rem;
    padding-top: 6.25rem;
    padding-bottom: 6.25rem;
    &__costs {
        &-item {
            padding-bottom: 1rem;
        }
        &-total {
            font-family: $playfair-font;
            font-size: $font-30;
        }
    }
    &__voucher {
        background-color: $backgroundgrey;
        border-radius: 10px;
        margin-top: 2.1875rem;
        &-text {
            font-family: $playfair-font;
            font-size: $font-20;
            padding: 0.8125rem 0 0.875rem 1.25rem;
        }
        &-button {
            background-color: $secondary;
            border-radius: 3.125rem;
            width: 7.8125rem;
            height: 2.375rem;
            margin-right: 0.6875rem;
            color: white;
        }
    }
    &__payingMethods {
        padding-top: 2.625rem;
        &-item {
            grid-row-gap: 0.1875rem;
            padding-left: 3.4375rem;
            height: 1.875rem;
            padding-bottom: 1.5625rem;
            font-size: 1.375rem;
            &-info {
                color: $secondary;
                font-size: 1rem;
            }
            input {
                position: absolute;
                opacity: 0;
                cursor: pointer;
            }
            .checkmark {
                position: absolute;
                top: 0;
                left: 0;
                height: 1.875rem;
                width: 1.875rem;
                background-color: $secondary;
                border-radius: 50%;
            }
            &:hover input ~ .checkmark {
                background-color: #ccc;
            }
            input:checked ~ .checkmark {
                background-color: $orange;
            }
            .checkmark:after {
                content: "";
                position: absolute;
                display: none;
            }
            input:checked ~ .checkmark:after {
                display: block;
            }
            .checkmark:after {
                top: 0.375rem;
                left: 0.375rem;
                width: 1.125rem;
                height: 1.125rem;
                border-radius: 50%;
                background: white;
            }
        }
    }
    &__rezervovat {
        border: 0;
        width: 14.9375rem;
        font-size: 1.5625rem;
        height: 3.75rem;
        border-radius: 3.125rem;
        margin-top: 2.625rem;
        background-color: $orange;
        color: white;
    }
}
@media only screen and (min-width: $md-breakpoint) {
    .wrap {
        border-radius: 1.875rem;
    }
    .SummaryBlock {
        font-size: $font-20;
        &__costs {
            &-item {
                div {
                    display: flex;
                }
            }
            &-total {
                font-family: $playfair-font;
                font-size: $font-35;
            }
        }
        &__voucher {
            margin-top: 3.5625rem;
            &-text {
                font-size: 1.5625rem;
            }
            &-button {
                width: 5.75rem;
            }
        }
        &__payingMethods {
            padding-top: 3.4375rem;
            &-item {
                display: flex;
                padding-bottom: 1.125rem;
            }
        }
        &__rezervovat {
            margin-top: 5.625rem;
        }
    }
}
