@import "@/scss/main.scss";

.containerWave {
    width: 100%;
    min-width: 42.5rem;
    height: 1.375rem;
    fill: $medium-beige;
    background: white;
}
.contain {
    color: $deepblue;
    background: $medium-beige;
    .content {
        max-width: 60.625rem;
        padding-right: 1.875rem;
        padding-left: 1.875rem;
        .carouselContainer {
            .footer__carousel {
                &-nextBtn,
                &-prevBtn {
                    top: 5rem;
                    width: 3.75rem;
                    height: 3.75rem;
                    background: $white;
                    border-radius: 50%;
                    transition: $transition-fast;
                    svg {
                        width: 2.25rem;
                        height: 2.25rem;
                        fill: $deepblue;
                    }
                }
                &-prevBtn {
                    transform: rotate(180deg);
                }
                &-slide {
                    font-weight: 300;
                    font-size: 1.5625rem;
                    line-height: 2.3438rem;
                    width: 100%;
                }
            }
            padding-top: 2.5rem;
            .buttons {
                padding-top: 1.875rem;
                grid-column-gap: 1.875rem;
            }
            .wave {
                padding-top: 3.125rem;
                padding-bottom: 1.625rem;
                width: 100%;
                height: 1.1875rem;
                fill: black;
                fill-opacity: 0.09;
            }
        }
        a {
            color: $deepblue;
        }
        &__number {
            font-family: $playfair-font;
            font-size: 2.8125rem;
            letter-spacing: -0.03em;
            padding-bottom: 2.5625rem;
        }

        &__links {
            padding-top: 2.8125rem;
            padding-bottom: 3.75rem;
            a {
                margin-left: 0.625rem;
                margin-right: 0.625rem;
            }
            svg {
                width: 3.125rem;
                height: 3.125rem;
                fill: $deepblue;
            }
        }
    }
}
@media only screen and (min-width: $md-breakpoint) {
    .containerWave {
        width: 100%;
        height: 1.375rem;
    }
    .contain {
        .content {
            .carouselContainer {
                padding-top: 8.1875rem;
                .footer__carousel {
                    &-nextBtn,
                    &-prevBtn {
                        z-index: 1;
                        &:hover {
                            width: 4.375rem;
                            height: 4.375rem;
                            bottom: 49.5%;
                        }
                    }
                    &-slide {
                        font-weight: 300;
                        font-size: 3.125rem;
                        line-height: 150%;
                        width: 48.125rem;
                    }
                }
                .buttons {
                    padding-top: 0px;
                    margin-top: -6.25rem;
                    height: 4.375rem;
                    justify-content: space-between;
                }
                .wave {
                    padding-top: 3.125rem;
                    padding-bottom: 2.75rem;
                    width: 100%;
                    height: min-content;
                }
            }
            &__number {
                font-size: 6.25rem;
                line-height: 8.3125rem;
            }
        }
    }
}
