@import "@/scss/main.scss";

.opened {
    height: 0;
    background-color: $medium-beige;
    border-radius: 1.875rem;
    font-size: $font-15;
    transition: $transition;
    z-index: 1;
    &__inner {
        margin: 1.5625rem;
        &-select {
            background: $darkdarkblue;
            width: 100%;
            height: 3.125rem;
            border-radius: 1.875rem;
            color: white;
            margin-bottom: 1.25rem;
            &:hover {
                background: #2a2746;
            }
            .pa {
                padding-left: 2.1875rem;
            }
            svg {
                width: 1.5rem;
                height: 1.5rem;
                padding-right: 0.9375rem;
                stroke: $white;
            }
        }
        &-timeframes {
            .timeframe,
            .timeframeReserved {
                padding-left: 2.1875rem;
                padding-right: 0.9375rem;
                display: flex;
                justify-content: space-between;
                align-items: center;
                height: 2.375rem;
                color: $deepblue;
                padding-top: 0.6875rem;
                padding-bottom: 0.6875rem;
                border-radius: 1.875rem;
                &:hover {
                    cursor: pointer;
                    background: #eeebe8;
                }
                .rezervaceButton {
                    background: $orange;
                    color: white;
                    border-radius: 3.125rem;
                    width: 7.8125rem;
                    height: 2.375rem;
                    pointer-events: none;
                }
            }
            .timeframeReserved {
                color: rgba(0, 0, 0, 0.25);
                &:hover {
                    cursor: initial;
                    background: initial;
                }
                .rezervaceButton {
                    background: rgba(0, 0, 0, 0.03);
                    color: rgba(0, 0, 0, 0.3);
                }
            }
        }
    }
}
@media only screen and (min-width: $md-breakpoint) {
    .opened {
        font-size: $font-20;
    }
}
